body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#fullpage {
  text-align: center;
}
.section .hi.animated .text {
  font-size: 75px;
  color: #f35626;
  background-image: -webkit-linear-gradient(92deg, #f35626, #feab3a);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: hue 60s infinite linear;
          animation: hue 60s infinite linear;
}

.section .hi.animated.static {
  position: absolute;
  top: 20px;
  left: 20px;
}

.section .hi.animated.static .text {
  font-size: 30px;
  width: 80px;
}

.section .name.animated.hidden{
  display: none;
}

.section .name.animated .text {
  font-size: 32px;
}

.section .nick.animated {
  margin-top: -16px;
}

.section .nick.animated.hidden{
  display: none;
}

.section .nick.animated .text {
  font-size: 32px;
}

.section .avatar.animated.hidden{
  display: none;
}

.section .avatar.animated .circle{
  border-color: #000;
  border-style: solid;
  border-radius: 100%;
  border-width: 1px;
  width: 150px;
  height: 150px;
  margin: -150px auto 40px auto;
}

@-webkit-keyframes hue {
  from {
    -webkit-filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
  }
}

@keyframes hue {
  from {
    -webkit-filter: hue-rotate(0deg);
  }
  to {
    -webkit-filter: hue-rotate(-360deg);
  }
}
.portfolio-container {
  color: #fff;
}
